@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
}

.main-container {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.nav-container {
    width: 550px;
    height: 55px;
    background: #F55A5A;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 7px;
    color: #fff;
    margin-bottom: 36px;
}

.nav-logo {
    width: 24px;
    height: 24px;
}

.card-container {
    display: flex;
    column-gap: 19px;
    color: #2B283A;
    margin: 18px 0;
}

.card-img {
    width: 125px;
    height: 170px;
    border-radius: 5px;
    /* object-fit: fill; */
    /* object-fit: contain; */
    object-fit: cover;
}

.card-location {
    display: flex;
    font-size: 12px;
    align-items: center;
}

.card-location-mark {
    width: 20px;
    margin-right: 5px;
}

.card-location-country {
    margin-right: 12px;
}

.card-location-link {
    color: #918E9B;
}

.card-title {
    font-size: 25px;
    line-height: 30px;
    font-weight: 700;
    height: 40px;
    margin-top: 7px;
}

.card-date {
    display: flex;
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 8px;
}

.card-text {
    width: 326px;
    font-size: 12px;
    line-height: 15px;
}

.card-line {
    width: calc(550px - 80px);
    border-color: #F5F5F5;
}

.cards-container {
    padding: 0 40px;
}